import React from 'react';
import {useState} from 'react';
import './Navbar.css';
export function Navbar () {
    const [isActive, setActive] = useState(false);
    const c1 = "{";
    const c2 = "}";
    const curly = {
        color: "#33b0a8"
    }
    const Toggle = () => {
        setActive(!isActive);
    }
    return (
        <div className="navigation">
            <label className="logo"><label style={curly}>{c1}</label>MANOJ KUMAWAT<label style={curly}>{c2}</label></label>
            <button onClick={Toggle} className="mobile-icon-button">
                <i className="bi bi-list-nested mobile-icon"></i>
            </button>
            
            <ul className={`${isActive ? "active desktop":"desktop"}`}>
                <li className="menu-items">BLOGS</li>
                <li className="menu-items">PROJECTS</li>
                <li className="menu-items">ABOUT</li>
                <li className="menu-items">CONTACT</li>
            </ul>
        </div>
    );
}